.premier {
  max-width: 140rem;
  margin: auto;
}

.title-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #ced4da;
}

.title {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 2rem;
}

.title h2 {
  font-size: 2.4rem;
}
.title p {
  font-size: 2rem;
}

.image {
  max-width: 80rem;
  max-height: 60rem;
}

.image img {
  width: 100%;
  height: 100%;
}
