.link {
  color: #0b7285;
  font-size: 2rem;
  cursor: pointer;
  padding: 1rem 2rem;
}

.link:hover {
  background-color: black;
}

.link a,
.link a:active,
.link a:visited {
  color: #fff;
  text-decoration: none;
}

@media (max-width: 37.9em) {
  .link {
    font-size: 1.4rem;
    padding: 0 1rem;
  }
}
